<template>
    <div class="my-popups modal right fade show" style="display:block" role="dialog">
        <div class="modal d-block " tabindex="-1" role="dialog">
            <div class="modal-dialog doc-setup-slide">
                <div class="modal-content">
                    <div class="modal-header mheader-user py-0">
                        <button type="button" class="close" @click="close()">
                            <i aria-hidden="true" class="fa fa-times"></i>
                        </button>
                        <h4 v-if="popupCondition == 'Configuration'" class="modal-title py-3">{{ $t('Configuration') }}</h4>
                        <h4 v-if="popupCondition == 'setup'" class="modal-title py-3">{{ $t('SetupTimerTrack') }}</h4>
                    </div>
                    <div class="modal-body bg-white">
                        <section class="container-fluid p-0">
                            <div class="left-menu-tab new_tab">
                                <div v-if="popupCondition == 'Configuration'">
                                    <div class="col-md-12 p-0">
                                        <div class="theme-primary partition">
                                            <span class="p-name text-white">{{ $t("Configuration") }}</span>
                                        </div>
                                    </div>
                                    <div class="pt-2 mb-1 w-100 d-none d-lg-block">
                                        <div class="input-group customsearchbox">
                                            <input type="text" maxlength="50" placeholder="Search module"
                                                class="form-control searchcustomfilter border-right-0"
                                                autocomplete="off" v-model="searchQuery"
                                                @keyup="filteredCustomField($event)" />
                                            <div class="input-group-append">
                                                <span class="input-group-text bg-transparent bg-white border-left-0"><i
                                                        class="fa fa-search" @click="filteredCustomField"></i></span>
                                            </div>
                                        </div>
                                    </div>
                                    <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="upper"
                                        role="tablist">
                                        <li class="nav-item" v-for="item in moduleddata" :key="item">
                                            <a id="leftmenu" class="nav-link left-menu " @click="activateTab(item)"
                                                :class="{ 'active': selectedModule === item }">
                                                <i id="itemdata" v-bind:class="item.classicon" class="px-2"></i>
                                                {{ item.moduleTitle }}
                                            </a>
                                        </li>
                                    </ul>
                                </div>
                                <div class="col-md-12 pb-0 " v-if="popupCondition == 'setup'">

                                    <div class="row">
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                            <div class="row">
                                                <div class="col-8 pr-0 font-weight-medium">{{ $t('CompleteYourProfile')
                                                    }}</div>

                                                <div class="col-4 pl-0 text-right"><small><i
                                                            class="fa fa-check-circle pr-1"
                                                            v-bind:class="{ 'text-success': SetupProgresText == 'Completed', 'text-muted': SetupProgresText !== 'Completed' }"></i>{{
                            SetupProgresText }}</small>
                                                </div>
                                            </div>
                                            <div class="progress smaller my-2">
                                                <div s
                                                    class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused"
                                                    id="divConsumendStorage" :class="`${progressBarColor}`"
                                                    :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`"
                                                    :title="SetupProgress">{{ SetupProgress }}%</div>

                                            </div>
                                            <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                                <ul>
                                                    <li>
                                                        <b id="divConsumendStorage"><span>Progress {{ CurrentTab }} of
                                                                4</span></b>
                                                    </li>
                                                </ul>
                                            </div>




                                        </div>
                                    </div>
                                <div class="row">
                                    <div  class="col-md-12 p-0 ">
                                        <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="upper" role="tablist">
                                        <li class="nav-item onlyhover">
                                            <a id="leftmenu" class="nav-link left-menu" data-toggle="collapse" data-target="#innerui" >
                                                <i class="fa fa-thumbs-up text-secondary font-20 px-2"></i>
                                                <span class="font-14" style="font-weight:500;">{{ $t('TimeApproval') }}</span>
                                                <i class="fa fa-angle-down float-right mr-2 mt-1"></i> 
                                            </a>
                                            <ul id="innerui" class="nav nav-pills flex-column left-tab collapse">
                                                <li class="nav-item left-menu"><a class="ml-4 pl-3" @click="activeTab('Approvalgroup')"><i class="fa fa-caret-right mr-2"></i>
                                                {{ $t('ApprovalGroup') }}</a></li>
                                                <li class="nav-item left-menu"> <a class="ml-4 pl-3" @click="activeTab('ApprovalChain')"><i class="fa fa-caret-right mr-2"></i>
                                                {{ $t('ApprovalChain') }}</a></li>
                                                <li class="nav-item left-menu"><a class="ml-4 pl-3" @click="activeTab('ApprovalRule')"><i class="fa fa-caret-right mr-2"></i>
                                                {{ $t('ApprovalRule') }}</a></li>
                                            </ul>
                                        </li>
                                    </ul>
                                </div>
                                   
                                </div></div>


                            </div>
                    
                    <div class="right-content">

                        <div class="col-md-12 p-0" v-if="popupCondition == 'Configuration'">
                            <Configuration :CurrentTab="CurrentTab" v-if="popupCondition == 'Configuration'"
                                :moduleKeys="moduleKeys" :modulename="modulename" :dynamicClass="dynamicclass"
                                :showsavebutton="showsavebutton" :activeKey="activeKey">
                            </Configuration>
                        </div>
                        <div class="col-md-12 p-0" v-if="popupCondition == 'setup'">
                            <Approval v-if="popupCondition == 'setup'" :activeKey="activeKey">
                            </Approval>
                        </div>
                    </div>

                    </section>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
import Configuration from "../../views/AdvanceSetup/Configuration.vue"
import Approval from "../AdvanceSetup/Approval.vue"
import DataService from '../../services/DataService';
export default {
    components: {
        Configuration,
        Approval
    },
    data() {


        return {
            step: 1,
            click: 'SetupAsset',
            CurrentTab: 1,
            IsClientStoragesCopmleted: false,


            activeKey: 0,
            moduleddata: [],
            moduleddataAll: [],
            searchQuery: "",
            modulename: "",
            moduletitle: [],
            classicon: [],
            activeKey: '',
            moduleKeys: [],
            validate: [],
            saveArray: [],
            showsavebutton: false,
            classadded: '',
            isLoading: false,
            dynamicclass: '',
            selectedModule: null,
            IsSearchItem: false,
            id: null,
            attachmentDiv: '',
            blobUrl: '',
            imgPath: '',
            UserType: '',
            CompanyLogoCoreConfigId: 0,
            timeZoneData: [],
            getTimeZone: '',
            timeZoneKeys: 0,
            Month: '',
            year: '',
            Number: '',
            arraydata: [],
            defaultdata: [],
            opennumbering: false,
            CheckBoxClass: '',
            salesorderdata: [],
            shiftData: [],
            designData: [],
            departData: [],
            locationData: [],
            currencyData: [],
            SetupProgress: 0,
            progressBarColor: '',
        }
    },
    created() {
        var vm = this;

        vm.GetLeftModulesdata();
    },
    computed: {
        SetupProgresText() {
            return this.SetupProgress == 0 ? 'Not Started' : (this.SetupProgress == 100 ? 'Completed' : 'In Progress');
        }
    },
    mounted: function () {
        var vm = this;
        $('#modalManageView').on('blur', function () {
            this.IsVisible = false;
        });
    },
    props: {
        IsVisible: {
            type: Boolean,
            required: true
        },
        popupCondition: {
            type: String,
            required: false
        }
    },
    watch: {
        IsVisible: {
            handler(val, older) {
                if (val) {
                    this.IsVisible = val
                }

            }
        }
    },
    methods: {
        close: function () {
            this.$parent.IsshowConfiguration = false;
        },
        SkipTab: function (s) {
            debugger
            var vm = this;
            vm.step = s;
            vm.CurrentTab = s;
        },
        BackToStep: function (s) {
            var vm = this;
            vm.step = s;
        },
        async GetLeftModulesdata() {
            var vm = this;
            let param = `moduleName=timer`
            await DataService.GetAllModule(param).then((response) => {
                vm.moduleddata = response.data;
                vm.moduleddataAll = vm.moduleddata;
                vm.moduleddata.forEach((item) => {
                    switch (item.moduleTitle) {
                        case "Approval":
                            item.classicon = "fa fa-thumbs-up";
                            break;
                        
                        case "Time Tracker":
                            item.classicon = "fa fa-clock-o";
                            break;
                        case "Timesheet":
                            item.classicon = "fa fa-calendar";
                            break;
                       
                        case "Work Diary":
                            item.classicon = "fa fa-picture-o";
                            break;
                        default:
                            item.classicon = "fa fa-pencil";
                    }
                });
                if (vm.moduleddata.length > 0) {
                    setTimeout(function () {
                        vm.activateTab(vm.moduleddata[0]);
                    }, 100);
                }
            });
        },
        async activateTab(item) {
            var vm = this;
            vm.isLoading = true;
            if (item != undefined) {
                vm.dynamicclass = item.classicon
                vm.showsavebutton = true;
                vm.modulename = item.moduleTitle;
                vm.activeKey = item.moduleId;
                vm.selectedModule = item;
            }
            var params = `moduleId=${vm.activeKey}&IsWizard=`;
            await DataService.GetKeys(params).then((response) => {
                if (response.data.keyList.length > 0) {
                    vm.moduleKeys = response.data.keyList

                    if (vm.moduleKeys.length > 5 && vm.moduleKeys[5]) {
                        vm.imgPath = vm.moduleKeys[5].coreConfigValue || '';
                        vm.CompanyLogoCoreConfigId = vm.moduleKeys[5].coreConfigId || '';
                    } else {

                        vm.imgPath = '';
                        vm.CompanyLogoCoreConfigId = '';
                    }
                    // vm.GetBlobUrl();
                    // vm.GetTimeZone();
                    // vm.GetDefaultTempData();
                    // vm.GetSalesOrder();
                    // vm.GetShiftData();
                    // vm.GetDesignationData();
                    // vm.GetLocation();
                    // vm.GetDepartmentData();
                    // vm.GetCurrencyData();
                }
                vm.isLoading = false;

            })

        },
        async activeTab(item){
            this.activeKey=item
            console.log("active",this.activeKey)
        },
        filteredCustomField: function (e) {
            var vm = this
            if (vm.searchQuery == '') {
                vm.moduleddata = vm.moduleddataAll;
            } else {
                let CheckIfExist = vm.moduleddataAll.filter(item => item.moduleTitle.toLowerCase().includes(vm.searchQuery.toLowerCase()));
                vm.moduleddata = CheckIfExist;
            }
            if (vm.moduleddata.length > 0) {
                setTimeout(function () {
                    vm.activateTab(vm.moduleddata[0]);
                }, 100);
            }
        },
    }
}
</script>
<style>
ul li.nav-item.onlyhover a.left-menu:hover{background:none!important; color:#868686!important;}
ul#innerui li.left-menu a{font-size:14px;font-weight:500; color: #868686!important;}
ul#innerui li.left-menu:hover{background:#e0f2ff!important; color:#24a5ff!important;}
ul#innerui li.left-menu:hover a{color:#24a5ff!important; font-weight:500;}

ul#innerui li.left-menu.active{background:#e0f2ff!important; color:#24a5ff!important;}
ul#innerui li.left-menu.active a{color:#24a5ff!important; font-weight:500;}
</style>