        },
        closeenablesectionpopup: function () {
            var vm = this;
            vm.EnableSectionspop = false;
        },
        RedirectToLinks: function (redirectType) {
            var envLink = process.env;
            if (redirectType == "HolidayBank") {
                var link = '/HolidayBank/Index'
                window.open(link);
                return false
            }
            if (redirectType == "ManageTags") {
                var link ='/tag/add'
                window.open(link);
                return false
            }
            if (redirectType == "Tags") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Tag/Index'
                window.open(link);
                return false
            }
            // if (redirectType == "Workingweek") {
            //     var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Week/manage'
            //     window.open(link);
            //     return false
            // }
            if (redirectType == "WorkingDay") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/WorkDay/Manage'
                window.open(link);
                return false
            }
            if (redirectType == "Break") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Break/Index'
                window.open(link);
                return false
            }
            if (redirectType == "CompanyHolidays") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/CompanyHoliday/Index'
                window.open(link);
                return false
            }
            if (redirectType == "ApprovalGroup") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/ApprovalGroup/Index'
                window.open(link);
                return false
            }
            if (redirectType == "ApprovalChain") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/ApprovalChain/Index'
                window.open(link);
                return false
            }
            if (redirectType == "ApprovalRule") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/ApprovalRule/Index'
                window.open(link);
                return false
            }
            if (redirectType == "AuditRule") {
                var link = envLink.VUE_APP_CURRENT_SITE_ROOT_URL + '/Audit/Index'
                window.open(link);
                return false
            }
            
        }
    }
}
</script>
